
import { defineComponent } from "vue";
import { Story } from "@/store";
import {
  IonCard,
  IonCardContent,
  IonChip,
  IonIcon,
  IonLabel,
} from "@ionic/vue";
import { addIcons } from 'ionicons'
import { person, star, time } from 'ionicons/icons'
addIcons({ person, star, time  })
export default defineComponent({
  components: { IonCard, IonCardContent, IonChip, IonIcon, IonLabel },
  name: "StoryComponent",
  props: {
    story: {
      type: Object as () => Story,
    },
  },
  setup() {
    return { person, star, time };
  },
